import React, { useState, useEffect } from 'react';
import AuthServiceWrapper from '../components/AuthServiceWrapper';
import InputWithLabel from '../components/InputWithLabel';
import styled from '@emotion/styled';
import Button from '../components/Button';
import { useHistory } from 'react-router';
import Modal from '../components/Modal';
import { api } from '../utils/api';
import auth from '../utils/auth';

const FindPassword = () => {
  const history = useHistory();

  const [info, setInfo] = useState({ email: '', certification_code: '' });
  const [isSendedCode, setIsSendedCode] = useState(false);
  const [isSendedModalOpen, setIsSendedModalOpen] = useState(false);

  const [errorMessage, setErrorMessage] = useState('');
  const [isErrorModalOpen, setIsErrorModalOpen] = useState(false);

  useEffect(() => {
    if (isSendedCode) {
      setIsSendedModalOpen(true);
    }
  }, [isSendedCode]);

  const handleRequest = async () => {
    const {
      data: { alert, success },
    } = await api.post('/password/find', { email: info.email });
    if (alert) {
      setErrorMessage(alert);
      setIsErrorModalOpen(true);
    }
    if (success) setIsSendedCode(true);
  };

  const handleSubmit = async () => {
    const {
      data: { alert, data, success },
    } = await api.post('/password/check', {
      ...info,
    });
    if (alert) {
      setErrorMessage(alert);
      setIsErrorModalOpen(true);
    }
    if (success) {
      auth.setToken(data?.token?.accessToken);
      history.push('/auth/password');
      window.location.reload();
    }
    return;
  };

  const handleChange = (e: any) => {
    e.persist();
    setInfo((prevState) => ({ ...prevState, [e.target.name]: e.target.value }));
  };

  useEffect(() => {
    console.log(isErrorModalOpen);
  }, [isErrorModalOpen]);

  return (
    <>
      <AuthServiceWrapper
        title="비밀번호 찾기"
        info={
          <>
            잊어버린 비밀번호를 찾기 위해서
            <br />
            가입 시 작성한 휴대폰 번호를 입력해주세요.
          </>
        }
      >
        <InputWrap>
          <InputWithLabel
            name="email"
            label="휴대폰 번호"
            placeholder="휴대폰 번호를 입력해주세요."
            onChange={handleChange}
          />
          {isSendedCode && (
            <InputWithLabel
              name="certification_code"
              label="인증번호"
              placeholder="인증번호를 입력해주세요."
              info="03:58"
              onChange={handleChange}
            />
          )}
        </InputWrap>
        <ButtonWrap>
          <Button
            styleType="blue"
            onClick={() => (isSendedCode ? handleSubmit() : handleRequest())}
          >
            {isSendedCode ? '새로운 비밀번호 설정' : '인증번호 발송'}
          </Button>
          <Button styleType="outline" onClick={() => history.goBack()}>
            뒤로
          </Button>
        </ButtonWrap>
      </AuthServiceWrapper>
      <Modal
        open={isSendedModalOpen}
        onClose={() => setIsSendedModalOpen(false)}
        firstButtonLabel="확인"
        onClickFirstButton={() => setIsSendedModalOpen(false)}
        title="인증번호 발송"
        content={
          <>
            입력하신 휴대폰 번호로
            <br />
            인증번호를 발송하였습니다.
          </>
        }
      />
      <Modal
        open={isErrorModalOpen}
        onClose={() => setIsErrorModalOpen(false)}
        firstButtonLabel="확인"
        onClickFirstButton={() => setIsErrorModalOpen(false)}
        title="에러"
        content={errorMessage}
      />
    </>
  );
};

const InputWrap = styled.div`
  & > * {
    margin-bottom: 20px;
  }
`;

const ButtonWrap = styled.div`
  margin-top: 50px;

  & > * {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }
`;

export default FindPassword;
